<template>
  <Container>
    <form>
      <fg-checkbox
        label="Hide this event"
        @changeValue="handleSetEvent"
        name="isHidden"
        :value="form.isHidden"
      />
      <hr>
      <ImageUplodaer
        title="Add image"
        uploading-text="Uploading..."
        @uploadPoster="setPoster"
        :poster="form.poster"
      />
      <hr>
      <ImageUplodaer
        title="Add coupon image"
        uploading-text="Uploading..."
        @uploadPoster="value =>
          updateEventForm({ value, key: 'coupon_image' })"
        :poster="form.coupon_image"
        parentInputId="coupon"
      />
      <hr>
      <fg-input 
        type="text"
        v-for="({
          label,
          value,
          subValue,
          name,
          placeholder,
          showedKey
        }, key) in fields"
        :key="key"
        :label="label"
        :placeholder="placeholder"
        @changeValue="handleSetEvent"
        :name="name"
        :value="form[value]"
      >
        <span v-if="value === 'location'">{{form.location.title}}</span>
        <cascader
          :name="name"
          :showedKey="showedKey"
          :options="getOptions"
          @handleChoose="handleChoose"
          v-if="subedKey === name"
        />
      </fg-input>
      <fg-textarea 
        label="Event description"
        @changeValue="handleSetEvent"
        name="description"
        :value="form.description"
        placeholder="Enter description of event"
      />
      <Timer
        @setData="handleSetEventTime"
        :data="form.timeData"
      />
      <div class="text-center">
        <button 
          type="submit"
          class="btn btn-info btn-fill btn-wd" 
          @click.prevent="submitForm"
        >
          {{id ? 'Update' : 'Create'}}
        </button>
        <router-link 
          to="/events/list" 
          class="btn btn-danger btn-fill btn-wd"
        >
          Back
        </router-link>
      </div>
    </form> 
  </Container>
</template>

<script>
import { mapActions, mapState, mapMutations, mapGetters } from 'vuex'
import { types, fields } from './TableMock.js'

export default {
  data: () => ({
    id: '',
    types,
    fields,
    subedKey: 'location'
  }),
  components: {
    ImageUplodaer: () => import('ui/ImageUploaderPlugin/ImageUploader.vue'),
    Timer: () => import('./TimePicker.vue'),
    Container: () => import('ui/Container/Container')
  },
  mounted() {
    this.id = this.$route.params.id
    if (this.id) {
      this.fetchEvent({ id: this.id })
    }
  },
  destroyed() {
    this.clearState()
  },
  computed: {
    ...mapState({
      form: state => state.events.event,
      categories: state => state.events.events
    }),
    ...mapGetters({
      foundedLocations: 'searchResult',
      foundedCategories: 'foundedCategories'
    }),
    getOptions() {
      return this.subedKey === 'category'
        ? this.foundedCategories
        : this.foundedLocations
    }
  },
  methods: {
    submitForm () {
      const id = this.id ? this.id : ''
      id ? this.updateEvent({ id }) : this.createEvent()
      this.$router.push('/events/list')
    },
    ...mapActions({
      fetchEvent: 'fetchEvent',
      createEvent: 'createEvent',
      updateEvent: 'updateEvent',
      findLocation: 'findLocation',
      findCategory: 'findCategory'
    }),
    ...mapMutations({
      resetForm: 'resetEvent',
      setPoster: 'setPoster',
      updateEventForm: 'updateEventForm',
      setSearchResult: 'setSearchResult',
      setFoundedCategories: 'setFoundedCategories',
      clearState: 'resetEvent'
    }),
    handleChoose(value, key) {
      const getName = key === 'category'
        ? 'title'
        : 'title'
      this.updateEventForm({ value: value[getName], key })
      this.updateEventForm({ value, key: `${key}_id` })
      this.setSearchResult([])
      this.setFoundedCategories([])
    },
    handleSetEventTime(key, value) {
      const payload = {
        key,
        value
      }
      this.updateEventForm(payload)
    },
    handleSetEvent(value, key) {
      this.subedKey = key
      const payload = {
        key,
        value
      }
      this.updateEventForm(payload)
      if (key === 'location') {
        this.findLocation(value)
      }
      if (key === 'category') {
        this.findCategory(value)
      }
    }
  }
}
</script>



